.Header {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    border: 1px solid #d3d3d386;
    box-sizing: border-box;
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    left: 1rem;
    padding: 1rem;
    border-radius: 1rem;

    a {
        text-align: center;
        cursor: pointer;
        color: #d3d3d386;
    }

    .active {
        color: var(--color2);
    }
}