code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

* {
    margin: 0;
    list-style: none;
    padding: 0;
    border: none;
}

.fl4 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.err-text {
    color: rgb(255, 82, 82);
}

.input-color {
    color: var(--color2);
}

.expandText {
    button {
        border: none;
        color: rgb(18, 75, 180);
        cursor: pointer;
        font-size: 0.65rem;
    }
}

.App-Board {
    max-width: 425px;
    width: 425px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.notification {
    position: fixed;
    top: 8%;
    left: 50%;
    padding: 6px 1rem;
    transform: translateX(-50%);
    background: rgb(255, 255, 255);
    background: linear-gradient(0deg, #ffffff 0%, #2e2e2e 50%, #ffffff 100%);
    animation: eff 4s ease-in-out;

}

.Home-Page {
    margin-top: 1rem;
    width: 425px;
    max-width: 98vw;
    color: #fff;
    box-sizing: border-box;


    .Main {
        padding: 5vh 2vw;

        .coin-balance {
            padding: 15vh 0;

            svg {
                font-size: 48px;
            }

            p {
                font-size: xx-large;

                span {
                    font-size: small;
                }
            }

        }
    }

    .action {
        display: flex;
        flex-direction: column;

        gap: 0.4rem;

        a,
        div {
            max-width: 80vw;
            display: grid;
            align-items: center;
            grid-template-columns: 8% 85% auto;
            color: var(--color3);
            width: 95%;
            border-radius: 0.6rem;
            border: 1px solid var(--color3);
            padding: 0.8rem 0.7rem;
            cursor: pointer;
        }
    }

    .balance-history {
        position: fixed;
        inset: 0;
        overflow: hidden;


        .balance-history-main {
            position: absolute;
            top: 50%;
            right: 0;
            left: 0;
            bottom: 0;
            opacity: 1;
            // animation: bottomToTop 0.5s ease-in-out forwards;
            background-color: #1f1f1f;
            display: grid;
            overflow: hidden;



            .heading {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 1rem;

                svg {
                    cursor: pointer;
                    font-size: x-large;
                }
            }


            ul {
                margin-right: -20px;
                padding: 10px;
                overflow-y: auto;
                display: flex;
                flex-direction: column;
                gap: 0.3rem;

                .element {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding: 0.2rem 1rem;
                    border: 1px solid #d3d3d36e;
                    border-radius: 0.4rem;

                    h4 {
                        color: #f0cc00;
                    }

                    i {
                        font-size: 0.7rem;
                        opacity: 0.7;
                    }
                }
            }
        }
    }

    .slide-in {
        animation: bottomToTop 0.5s ease-in-out forwards;
    }

    /* Hiệu ứng trượt xuống khi ẩn */
    .slide-out {
        animation: topToBottom 0.5s ease-in-out forwards;
    }

}

.Daily-Container {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 97vw;


    i {
        font-size: x-small;
    }

    .streak-check-in {
        border: 3px solid #adadad3a;
        box-sizing: border-box;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 1vw 0;
    }

    .a-description {
        font-size: smaller;
    }

    .list-reward {
        margin-top: 1vh;
        display: grid;
        grid-template-columns: auto auto auto;



        .check-in-today {
            cursor: pointer;

            p {
                color: var(--color2);
            }

        }


        .check-in-item {
            display: grid;
            grid-template-columns: 35% auto;
            column-gap: 2px;
            align-items: center;
            color: #292929;
            padding: 0.2rem 0.5rem;
            margin: 0.4rem;
            border-radius: 0.7rem;
            box-sizing: border-box;
            position: relative;
            background: rgb(255, 255, 255);
            background: radial-gradient(circle, #ffffff3d 0%, #d3d3d3 60%);
            text-align: start;

            .coins {
                color: #f06000;
                font-size: small;
            }
        }

        #icon_coins {
            font-size: xx-large;
            grid-row: 1/5;
        }

        .check-in-item.checked {
            border: 1px solid var(--color2);

            .coins {
                color: #f06000;
            }

            p {
                color: var(--color2);
            }
        }

        #icon_checked {
            position: absolute;
            right: 5px;
            bottom: 3px;
            background-color: var(--color2);
            color: #ffffff;
            font-size: small;
            border-radius: 50%;
        }
    }

    #gift-check-in {
        margin: 3vh 0 2vh 0;
        font-size: 8.5rem;
        color: #d3d3d3;
    }

    .btn-gift {
        width: 14rem;
        height: 3rem;
        background-color: #adadad;
        border-radius: 8px;
        padding: 0.3rem;
    }
}

.Referral-Container {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 97vw;

    .card {
        padding: 1rem 1rem;
        margin-top: 1vh;
        border-radius: 12px;
        text-align: left;
        background-color: #3f3f3f;
        width: 82vw;

        .title--card {
            font-weight: bold;
        }

        .description {
            display: flex;
            gap: 4px;

            span {
                color: #009fcf;
                display: flex;
                align-items: center;
            }
        }
    }

    .link_ref_card {
        position: relative;

        i {
            font-size: small;
            user-select: text;
        }

        svg {
            position: absolute;
            font-size: x-large;
            top: 50%;
            right: 1rem;
            transform: translate(0, -50%);
            cursor: pointer;
        }
    }

    .title--friend-list {
        width: 82vw;
        display: flex;
        justify-content: space-between;
        margin-top: 2vh;

        svg {
            cursor: pointer;
        }
    }


}

.Register_Container {
    position: fixed;
    inset: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    p {
        text-align: center;
        font-size: large;
        word-wrap: break-word;
        overflow-wrap: break-word;
        padding: 0 5vw;
    }

    button {
        padding: 1rem 2rem;
        border-radius: 12px;
        font-size: large;
        cursor: pointer;
    }
}

.Task-Container {
    margin-top: 3vh;

    .unlimited-task-container,
    .daily-task-container {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;

        .Task-Element {
            background-color: #2e2e2e;
            padding: 0.5rem 0.3rem;
            border-radius: 0.6rem;
            display: grid;
            grid-template-columns: 10% auto 20% 3%;
            align-items: center;

            .icon {
                text-align: center;
                font-size: larger;
            }

            .mission--detail {
                font-size: small;

                .coin {
                    color: var(--color2);
                }
            }

            a {
                border: 1px solid var(--color2);
            }

            .link {
                width: 100%;
                text-align: center;
                padding: 0.3rem 0;
                color: var(--color2);
                border-radius: 0.5rem;
            }
        }
    }

    .option-earning {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 2rem 0;
        gap: 1rem;

        div {
            cursor: pointer;
        }

        .option-active {
            color: var(--color1);
        }
    }

}

.Stake-Container {
    margin-top: 4vh;

    .go-to-home {
        display: flex;
        align-items: center;
        color: var(--color2);
    }

    .List-products {
        margin-top: 1rem;

        .product {
            width: 70vw;
            max-width: 360px;
            border: 1px solid var(--color2);
            display: flex;
            gap: 0.3rem;
            padding: 0.4rem 1rem;
            align-items: center;
            margin: 0.3rem 0;
            justify-content: space-between;

            button {
                padding: 0.3rem 0.6rem;
                border-radius: 0.2rem;
                background: rgb(255, 255, 255);
                background: radial-gradient(circle, #b9d7fa 0%, #d3d3d3 60%);
                color: black;
                cursor: pointer;
            }
        }
    }

    .Staking-CPN {
        border: 2px solid var(--color2);
        background-color: #00212b;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 1rem;
    }

    .List-staking {
        display: flex;
        flex-direction: column;
        align-items: center;

        li {
            background-color: var(--color2);
            margin-top: 1rem;
            box-sizing: border-box;
            padding: 1rem 0.8rem;
            border-radius: 1rem;
            gap: 1.4rem;
            width: 80%;

            div {
                display: flex;
                justify-content: space-between;

                .time {
                    padding: 0.6rem 1rem;
                    color: var(--color2);
                    border-radius: 0.6rem;
                    background: rgba(255, 255, 255, 0.856);
                }
            }
        }
    }
}


.Staking-Container {
    padding: 1rem;
    width: 75vw;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .close-btn {
        color: var(--color2);
        position: absolute;
        right: 5%;
        top: 5%;
        cursor: pointer;
        border: 1px solid var(--color2);
        border-radius: 50%;
    }

    i {
        font-size: smaller;
        text-align: center;
    }

    form {
        width: 80%;
        margin-top: 1rem;
        border-radius: 1rem;

        #value {
            padding: 5px 10px;
            width: 100%;
            box-sizing: border-box;
            margin: 5px 0;
            font-weight: bold;
        }

        .clause {
            font-size: x-small;
        }

        .btn-s {
            background-color: var(--color2);
            width: 40%;
            padding: 5px 10px;
            margin: 10px 30%;
            color: black;
            cursor: pointer;
        }
    }
}

.progress-bar-container {
    width: 100%;
    background-color: #f3f3f3;
    /* Màu nền cho container của thanh tiến trình */
    border-radius: 5px;
    margin: 10px 0;
    overflow: hidden;

    .progress-bar {
        height: 5px;
        /* Chiều cao của thanh tiến trình */
        background-color: red;
        /* Màu của thanh tiến trình */
        width: 0;
        /* Giá trị ban đầu (0%) */
        line-height: 30p;
        /* Căn giữa văn bản */
        color: whit;
        /* Màu chữ hiển thị phần trăm */
        text-align: cente;
        /* Căn giữa chữ */
        border-radius: 5px;
        transition: width 0.3s eas;
        /* Hiệu ứng chuyển động mượt mà */
    }
}

.LeaderBoard-Container {
    max-width: 90vw;
    min-width: 90vw;
    margin-top: 1rem;

    .icon-top {
        svg {
            font-size: 48px;
        }
    }

    .myRank {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: var(--color3);
        color: #333;
        padding: 0.4rem 1rem;
        box-sizing: border-box;
        border-radius: 0.7rem;
        margin-top: 0.9rem;
    }

    ul {
        margin-top: 1.3rem;
        display: flex;
        flex-direction: column;
        gap: 0.3rem;

        .element {
            padding: 0.4rem 1rem;
            background-color: #333;
            border-radius: 0.7rem;
            display: flex;
            width: 100%;
            box-sizing: border-box;
            align-items: center;
            justify-content: space-between;

            p {
                font-size: small;
            }
        }
    }
}

.Start-Container {
    position: fixed;
    inset: 0;

    div {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .btn-start {
            background-color: #333;
            cursor: pointer;
            color: var(--color3);
            margin-top: 0.4rem;
            padding: 0.4rem 0.9rem;
            border-radius: 0.4rem;

            &:hover {
                background-color: #9be6eb50;
                color: var(--color1);
            }
        }
    }

    .reward-container {
        text-align: center;
        margin-top: 50px;
    }

    .reward {
        font-size: 50px;
        font-weight: bold;
        margin-bottom: 20px;
        transition: all 0.3s ease;
    }

    /* Hiệu ứng khi số random đang chạy */
    .reward.running {
        color: var(--color1);
        transform: scale(1.3);
        /* Tăng kích thước khi đang chạy */
    }

    .register-3 {
        width: 90%;

        i {
            font-size: small;
        }
    }
}